.intro {
  position: relative;
  height: 693px;
  background-color: var(--color-black);

  &::after,
  &::before {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
  }

  &::before {
    background: url("../img/intro_bg.png") top no-repeat;
    background-size: cover;
    opacity: 0.2;
  }

  &::after {
    position: absolute;
    top: 0;
    background-color: var(--color-purple);
    mix-blend-mode: multiply;
  }

  &__boxs {
    position: absolute;
    left: 50%;
    top: 50%;
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 15px;
    width: 1200px;
    height: 693px;
    transform: translate(-50%, -50%);

    &-one {
      max-width: 575px;
    }

    &-two {
      position: absolute;
      left: 52%;
      top: 30%;
      z-index: 2;
    }
  }

  &__title {
    font-family: "Montserrat", sans-serif;
    font-weight: 700;
    font-size: 48px;
    line-height: 1.16;
    color: var(--color-white);
  }

  &__descr {
    margin: 48px 0;
    padding-right: 30px;
    line-height: 1.4;
    color: var(--color-white);
  }
}

.button {
  display: flex;

  &__one {
    position: relative;
    margin-right: 16px;
    border-radius: 6px;
    padding: 12px 16px;
    background: var(--color-white);
    transition: background 0.3s;

    a {
      font-weight: 900;
      font-size: 12px;
      line-height: 2;
      letter-spacing: 1px;
      text-transform: uppercase;
      color: var(--color-purple);
      transition: color 0.3s, box-shadow 0.3s;

      &::before {
        content: "";
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
      }
    }

    &:hover {
      box-shadow: 0 0 55px -10px rgba(81, 1, 166, 0.56);
      background: var(--color-purple);
      transition: background 0.3s, box-shadow 0.3s;

      a {
        color: var(--color-white);
        transition: color 0.3s;
      }
    }
  }

  &__two {
    border-radius: 50%;
    width: 48px;
    height: 48px;
    background: var(--color-purple);
    transition: background 0.3s, box-shadow 0.3s;

    svg {
      margin-top: 2px;
      width: 24px;
      height: 24px;
      stroke: var(--color-white);
      transition: stroke 0.3s;
    }

    &:hover {
      box-shadow: 0 0 55px -10px rgba(81, 1, 166, 0.56);
      background: var(--color-mercury);
      transition: background 0.3s, box-shadow 0.3s;

      svg {
        stroke: var(--color-purple);
        transition: stroke 0.3s;
      }
    }
  }
}

.image {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 1%;
    display: block;
    border-radius: 50%;
    box-shadow: 0 0 55px -10px rgba(81, 1, 166, 0.15);
    background: radial-gradient(96.06% 96.06% at 100% 100%, #5101a6 22.68%, rgba(81, 1, 166, 0) 100%);
    filter: drop-shadow(24px 24px 80px rgba(56, 11, 103, 0.3));
    mix-blend-mode: color;
  }
}

.ellipse {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
  width: 100%;
  height: 693px;

  &__container {
    position: relative;
    width: 100%;
    height: 100vh;
  }

  .ellipses {
    position: absolute;
    z-index: 1;
    border: 1px solid var(--color-mercury);
    border-radius: 50%;
    width: 1145px;
    height: 1145px;
  }

  .ellipse__one {
    left: 53%;
    top: 50%;
    width: 1600px;
    height: 1600px;
    opacity: 0.15;
    transform: translate(-50%, -50%);
  }

  .ellipse__two {
    left: 56%;
    top: 35%;
    opacity: 0.15;
    transform: translate(-50%, -50%);
  }

  .ellipse__three {
    left: 35%;
    top: -15%;
    opacity: 0.05;
  }

  .ellipse__four {
    left: 49%;
    top: 24.5%;
    width: 640px;
    height: 640px;
    opacity: 0.05;
  }
}

.fills {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
  width: 100%;
  height: 693px;

  &__container {
    position: relative;
    width: 100%;
    height: 100vh;
  }

  .fill {
    &::before {
      content: "";
      position: absolute;
      width: 850px;
      height: 946px;
      box-shadow: -120px 2px 94px rgba(0, 0, 0, 0.13);
      transform: translate(-50%, -50%);
    }
  }

  .fills__one {
    &::before {
      left: 53%;
      top: 50%;
      background: url("../img/fill-1.png") center no-repeat;
      background-size: cover;
      opacity: 0.1;
    }
  }

  .fills__two {
    &::before {
      left: 65%;
      top: 50%;
      background: url("../img/fill.png") center no-repeat;
      background-size: cover;
      opacity: 0.05;
    }
  }

  .fills__three {
    &::before {
      left: 98%;
      top: 50%;
      background: url("../img/fill-1.png") center no-repeat;
      background-size: cover;
      opacity: 0.05;
    }
  }
}
