.header {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 3;
  padding: 20px 0;
  width: 100%;

  &__box {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__button {
    position: relative;
    border-radius: 6px;
    padding: 8px 12px;
    background: var(--color-white);

    a {
      font-weight: 900;
      font-size: 12px;
      line-height: 2;
      letter-spacing: 1px;
      text-transform: uppercase;
      color: var(--color-purple);

      &::before {
        content: "";
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
      }
    }

    &:hover {
      box-shadow: 0 0 55px -10px rgba(81, 1, 166, 0.56);
      background: var(--color-purple);
      transition: background 0.3s, box-shadow 0.3s;

      a {
        color: var(--color-white);
        transition: color 0.3s;
      }
    }
  }
}
