html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 20px;
  background-color: var(--color-white);
}

img {
  height: auto;
  max-width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 1024px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1200px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.header {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 3;
  padding: 20px 0;
  width: 100%;
}

.header__box {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__button {
  position: relative;
  border-radius: 6px;
  padding: 8px 12px;
  background: var(--color-white);
}

.header__button a {
  font-weight: 900;
  font-size: 12px;
  line-height: 2;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: var(--color-purple);
}

.header__button a::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.header__button:hover {
  box-shadow: 0 0 55px -10px rgba(81, 1, 166, 0.56);
  background: var(--color-purple);
  transition: background 0.3s, box-shadow 0.3s;
}

.header__button:hover a {
  color: var(--color-white);
  transition: color 0.3s;
}

.intro {
  position: relative;
  height: 693px;
  background-color: var(--color-black);
}

.intro::after, .intro::before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
}

.intro::before {
  background: url("../img/intro_bg.png") top no-repeat;
  background-size: cover;
  opacity: 0.2;
}

.intro::after {
  position: absolute;
  top: 0;
  background-color: var(--color-purple);
  mix-blend-mode: multiply;
}

.intro__boxs {
  position: absolute;
  left: 50%;
  top: 50%;
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 15px;
  width: 1200px;
  height: 693px;
  transform: translate(-50%, -50%);
}

.intro__boxs-one {
  max-width: 575px;
}

.intro__boxs-two {
  position: absolute;
  left: 52%;
  top: 30%;
  z-index: 2;
}

.intro__title {
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 48px;
  line-height: 1.16;
  color: var(--color-white);
}

.intro__descr {
  margin: 48px 0;
  padding-right: 30px;
  line-height: 1.4;
  color: var(--color-white);
}

.button {
  display: flex;
}

.button__one {
  position: relative;
  margin-right: 16px;
  border-radius: 6px;
  padding: 12px 16px;
  background: var(--color-white);
  transition: background 0.3s;
}

.button__one a {
  font-weight: 900;
  font-size: 12px;
  line-height: 2;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: var(--color-purple);
  transition: color 0.3s, box-shadow 0.3s;
}

.button__one a::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.button__one:hover {
  box-shadow: 0 0 55px -10px rgba(81, 1, 166, 0.56);
  background: var(--color-purple);
  transition: background 0.3s, box-shadow 0.3s;
}

.button__one:hover a {
  color: var(--color-white);
  transition: color 0.3s;
}

.button__two {
  border-radius: 50%;
  width: 48px;
  height: 48px;
  background: var(--color-purple);
  transition: background 0.3s, box-shadow 0.3s;
}

.button__two svg {
  margin-top: 2px;
  width: 24px;
  height: 24px;
  stroke: var(--color-white);
  transition: stroke 0.3s;
}

.button__two:hover {
  box-shadow: 0 0 55px -10px rgba(81, 1, 166, 0.56);
  background: var(--color-mercury);
  transition: background 0.3s, box-shadow 0.3s;
}

.button__two:hover svg {
  stroke: var(--color-purple);
  transition: stroke 0.3s;
}

.image {
  position: relative;
}

.image::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 1%;
  display: block;
  border-radius: 50%;
  box-shadow: 0 0 55px -10px rgba(81, 1, 166, 0.15);
  background: radial-gradient(96.06% 96.06% at 100% 100%, #5101a6 22.68%, rgba(81, 1, 166, 0) 100%);
  filter: drop-shadow(24px 24px 80px rgba(56, 11, 103, 0.3));
  mix-blend-mode: color;
}

.ellipse {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
  width: 100%;
  height: 693px;
}

.ellipse__container {
  position: relative;
  width: 100%;
  height: 100vh;
}

.ellipse .ellipses {
  position: absolute;
  z-index: 1;
  border: 1px solid var(--color-mercury);
  border-radius: 50%;
  width: 1145px;
  height: 1145px;
}

.ellipse .ellipse__one {
  left: 53%;
  top: 50%;
  width: 1600px;
  height: 1600px;
  opacity: 0.15;
  transform: translate(-50%, -50%);
}

.ellipse .ellipse__two {
  left: 56%;
  top: 35%;
  opacity: 0.15;
  transform: translate(-50%, -50%);
}

.ellipse .ellipse__three {
  left: 35%;
  top: -15%;
  opacity: 0.05;
}

.ellipse .ellipse__four {
  left: 49%;
  top: 24.5%;
  width: 640px;
  height: 640px;
  opacity: 0.05;
}

.fills {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
  width: 100%;
  height: 693px;
}

.fills__container {
  position: relative;
  width: 100%;
  height: 100vh;
}

.fills .fill::before {
  content: "";
  position: absolute;
  width: 850px;
  height: 946px;
  box-shadow: -120px 2px 94px rgba(0, 0, 0, 0.13);
  transform: translate(-50%, -50%);
}

.fills .fills__one::before {
  left: 53%;
  top: 50%;
  background: url("../img/fill-1.png") center no-repeat;
  background-size: cover;
  opacity: 0.1;
}

.fills .fills__two::before {
  left: 65%;
  top: 50%;
  background: url("../img/fill.png") center no-repeat;
  background-size: cover;
  opacity: 0.05;
}

.fills .fills__three::before {
  left: 98%;
  top: 50%;
  background: url("../img/fill-1.png") center no-repeat;
  background-size: cover;
  opacity: 0.05;
}

.about {
  padding-top: 168px;
  padding-bottom: 144px;
}

.about__container {
  position: relative;
}

.about__title {
  margin-bottom: 76px;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 40px;
  line-height: 1.2;
  text-align: center;
  color: var(--color-tolopea);
}

.about__nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 44px;
}

.about__list {
  display: flex;
}

.about__item:not(:last-child) {
  margin-right: 48px;
}

.about__link {
  position: relative;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.5;
  letter-spacing: 0.5px;
  color: var(--color-purple);
}

.about__link::before {
  content: "";
  position: absolute;
  left: 0;
  top: 25px;
  display: block;
  width: 0;
  height: 2px;
  background-color: var(--color-purple);
  transition: width 0.3s;
}

.about__link:hover::before {
  width: 100%;
  transition: width 0.3s;
}

.about__link--current::before {
  width: 100%;
}

.about__swiper {
  overflow: hidden;
  width: 100%;
}

.box__image {
  border-radius: 8px;
}

.box__title {
  margin-top: 24px;
  margin-bottom: 8px;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 2;
  letter-spacing: 0.01em;
  text-transform: uppercase;
  color: #5101a6;
}

.box__descr {
  padding-right: 18px;
  font-size: 13px;
  line-height: 1.5;
  color: var(--color-black);
}

.box-pag {
  position: absolute;
  left: 50%;
  bottom: -10%;
  transform: translateX(-50%);
}

.box-pag .swiper-pagination-bullet:not(:last-child) {
  margin-right: 16px;
}

.box-pag .swiper-pagination-bullet-active {
  background-color: var(--color-purple);
}

.about__button .about-btn-prev {
  margin-right: 16px;
  border-radius: 6px;
  width: 48px;
  height: 48px;
  background: var(--color-whisper);
}

.about__button .about-btn-prev svg {
  margin-top: 4px;
  width: 18px;
  height: 18px;
}

.about__button .about-btn-next {
  border-radius: 6px;
  width: 48px;
  height: 48px;
  background: var(--color-whisper);
}

.about__button .about-btn-next svg {
  margin-top: 4px;
  width: 18px;
  height: 18px;
}

.query {
  position: relative;
  padding-top: 75px;
  height: 540px;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), var(--color-purple);
}

.query__box {
  position: absolute;
  left: 50%;
  top: 50%;
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 15px;
  width: 1200px;
  height: 100%;
  transform: translate(-50%, -50%);
}

.query__info {
  width: 100%;
}

.query__title {
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 32px;
  line-height: 1.25;
  text-align: center;
  color: var(--color-white);
}

.query__descr {
  margin: 32px auto 50px;
  padding-right: 20px;
  width: 576px;
  font-size: 20px;
  line-height: 1.4;
  text-align: center;
  color: var(--color-white);
}

.form {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.form__one {
  position: relative;
  display: inline-block;
}

.form__one::after, .form__one::before {
  content: "";
  position: absolute;
  top: 1px;
  z-index: 2;
  border: 1px solid #bb6bd9;
  background-color: #bb6bd9;
}

.form__one::before {
  right: 1px;
  border-top-right-radius: 100px;
  border-bottom-right-radius: 100px;
  width: 210px;
}

.form__one::after {
  left: 1px;
  border-top-left-radius: 100px;
  border-bottom-left-radius: 100px;
  width: 15px;
}

.form__position {
  position: absolute;
  left: 6%;
  top: -20%;
  z-index: 2;
}

.form__name {
  padding: 0 15px;
  font-size: 12px;
  line-height: 2.6;
  color: var(--color-white);
}

.form__name-input {
  position: relative;
  outline: none;
  border: 2px solid #bb6bd9;
  border-top: 0 !important;
  border-radius: 6px;
  padding: 20px;
  padding-right: 100px;
  font-size: 14px;
  line-height: 2.6;
  color: var(--color-white);
  background-color: transparent;
}

.form__name-input::placeholder {
  font-weight: 700;
  font-size: 16px;
  line-height: 1.5;
  color: #bb6bd9;
}

.form__two {
  position: relative;
  display: inline-block;
}

.form__two::after, .form__two::before {
  content: "";
  position: absolute;
  top: 1px;
  z-index: 2;
  border: 1px solid #bb6bd9;
  background-color: #bb6bd9;
}

.form__two::before {
  right: 1px;
  border-top-right-radius: 100px;
  border-bottom-right-radius: 100px;
  width: 185px;
}

.form__two::after {
  left: 1px;
  border-top-left-radius: 100px;
  border-bottom-left-radius: 100px;
  width: 15px;
}

.form__three {
  display: inline-block;
}

.form__btn {
  border-radius: 6px;
  padding: 22px 80px;
  font-weight: 700;
  font-size: 16px;
  line-height: 2;
  text-transform: uppercase;
  color: var(--color-white);
  background: var(--color-purple);
  transition: color 0.3s, background 0.3s;
}

.form__btn:hover {
  color: var(--color-purple);
  background: var(--color-white);
  transition: all 0.3s;
}

.disk {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
  width: 100%;
  height: 540px;
}

.disk__container {
  position: relative;
  width: 100%;
  height: 100vh;
}

.disk .disks {
  position: absolute;
  z-index: 1;
  border: 1px solid var(--color-mercury);
  border-radius: 50%;
}

.disk .disk__one {
  left: 50%;
  top: 36%;
  width: 1144px;
  height: 1144px;
  opacity: 0.05;
  transform: translate(-50%, -50%);
}

.disk .disk__two {
  left: 50%;
  top: 36%;
  width: 640px;
  height: 640px;
  opacity: 0.05;
  transform: translate(-50%, -50%);
}

.arrows {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
  width: 100%;
  height: 693px;
}

