.about {
  padding-top: 168px;
  padding-bottom: 144px;

  &__container {
    position: relative;
  }

  &__title {
    margin-bottom: 76px;
    font-family: "Montserrat", sans-serif;
    font-weight: 700;
    font-size: 40px;
    line-height: 1.2;
    text-align: center;
    color: var(--color-tolopea);
  }

  &__nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 44px;
  }

  &__list {
    display: flex;
  }

  &__item:not(:last-child) {
    margin-right: 48px;
  }

  &__link {
    position: relative;
    font-weight: 700;
    font-size: 16px;
    line-height: 1.5;
    letter-spacing: 0.5px;
    color: var(--color-purple);

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 25px;
      display: block;
      width: 0;
      height: 2px;
      background-color: var(--color-purple);
      transition: width 0.3s;
    }

    &:hover {
      &::before {
        width: 100%;
        transition: width 0.3s;
      }
    }

    &--current {
      &::before {
        width: 100%;
      }
    }
  }

  &__swiper {
    overflow: hidden;
    width: 100%;
  }
}

.box {
  &__image {
    border-radius: 8px;
  }

  &__title {
    margin-top: 24px;
    margin-bottom: 8px;
    font-family: "Montserrat", sans-serif;
    font-weight: 700;
    font-size: 14px;
    line-height: 2;
    letter-spacing: 0.01em;
    text-transform: uppercase;
    color: #5101a6;
  }

  &__descr {
    padding-right: 18px;
    font-size: 13px;
    line-height: 1.5;
    color: var(--color-black);
  }
}

.box-pag {
  position: absolute;
  left: 50%;
  bottom: -10%;
  transform: translateX(-50%);

  .swiper-pagination-bullet:not(:last-child) {
    margin-right: 16px;
  }

  .swiper-pagination-bullet-active {
    background-color: var(--color-purple);
  }
}

.about__button {
  .about-btn-prev {
    margin-right: 16px;
    border-radius: 6px;
    width: 48px;
    height: 48px;
    background: var(--color-whisper);

    svg {
      margin-top: 4px;
      width: 18px;
      height: 18px;
    }
  }

  .about-btn-next {
    border-radius: 6px;
    width: 48px;
    height: 48px;
    background: var(--color-whisper);

    svg {
      margin-top: 4px;
      width: 18px;
      height: 18px;
    }
  }
}
