.query {
  position: relative;
  padding-top: 75px;
  height: 540px;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), var(--color-purple);

  &__box {
    position: absolute;
    left: 50%;
    top: 50%;
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 15px;
    width: 1200px;
    height: 100%;
    transform: translate(-50%, -50%);
  }

  &__info {
    width: 100%;
  }

  &__title {
    font-family: "Montserrat", sans-serif;
    font-weight: 700;
    font-size: 32px;
    line-height: 1.25;
    text-align: center;
    color: var(--color-white);
  }

  &__descr {
    margin: 32px auto 50px;
    padding-right: 20px;
    width: 576px;
    font-size: 20px;
    line-height: 1.4;
    text-align: center;
    color: var(--color-white);
  }
}

.form {
  display: flex;
  align-items: center;
  justify-content: space-between;

  &__one {
    position: relative;
    display: inline-block;

    &::after,
    &::before {
      content: "";
      position: absolute;
      top: 1px;
      z-index: 2;
      border: 1px solid #bb6bd9;
      background-color: #bb6bd9;
    }

    &::before {
      right: 1px;
      border-top-right-radius: 100px;
      border-bottom-right-radius: 100px;
      width: 210px;
    }

    &::after {
      left: 1px;
      border-top-left-radius: 100px;
      border-bottom-left-radius: 100px;
      width: 15px;
    }
  }

  &__position {
    position: absolute;
    left: 6%;
    top: -20%;
    z-index: 2;
  }

  &__name {
    padding: 0 15px;
    font-size: 12px;
    line-height: 2.6;
    color: var(--color-white);

    &-input {
      position: relative;
      outline: none;
      border: 2px solid #bb6bd9;
      border-top: 0 !important;
      border-radius: 6px;
      padding: 20px;
      padding-right: 100px;
      font-size: 14px;
      line-height: 2.6;
      color: var(--color-white);
      background-color: transparent;
    }

    &-input::placeholder {
      font-weight: 700;
      font-size: 16px;
      line-height: 1.5;
      color: #bb6bd9;
    }
  }

  &__two {
    position: relative;
    display: inline-block;

    &::after,
    &::before {
      content: "";
      position: absolute;
      top: 1px;
      z-index: 2;
      border: 1px solid #bb6bd9;
      background-color: #bb6bd9;
    }

    &::before {
      right: 1px;
      border-top-right-radius: 100px;
      border-bottom-right-radius: 100px;
      width: 185px;
    }

    &::after {
      left: 1px;
      border-top-left-radius: 100px;
      border-bottom-left-radius: 100px;
      width: 15px;
    }
  }

  &__three {
    display: inline-block;
  }

  &__btn {
    border-radius: 6px;
    padding: 22px 80px;
    font-weight: 700;
    font-size: 16px;
    line-height: 2;
    text-transform: uppercase;
    color: var(--color-white);
    background: var(--color-purple);
    transition: color 0.3s, background 0.3s;

    &:hover {
      color: var(--color-purple);
      background: var(--color-white);
      transition: all 0.3s;
    }
  }
}

.disk {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
  width: 100%;
  height: 540px;

  &__container {
    position: relative;
    width: 100%;
    height: 100vh;
  }

  .disks {
    position: absolute;
    z-index: 1;
    border: 1px solid var(--color-mercury);
    border-radius: 50%;
  }

  .disk__one {
    left: 50%;
    top: 36%;
    width: 1144px;
    height: 1144px;
    opacity: 0.05;
    transform: translate(-50%, -50%);
  }

  .disk__two {
    left: 50%;
    top: 36%;
    width: 640px;
    height: 640px;
    opacity: 0.05;
    transform: translate(-50%, -50%);
  }
}

.arrows {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
  width: 100%;
  height: 693px;

  &__container {
    position: relative;
    width: 100%;
    height: 100vh;
  }

  &__one {
    position: absolute;
    left: 30%;
    top: 50%;
    width: 850px;
    height: 100%;
    transform: translate(-50%, -50%);
  }

  &__two {
    position: absolute;
    left: 50%;
    top: 50%;
    width: 850px;
    height: 100%;
    transform: translate(-50%, -50%);
  }

  &__three {
    position: absolute;
    left: 65%;
    top: 50%;
    width: 850px;
    height: 100%;
    transform: translate(-50%, -50%);
  }
}

.arrow {
  box-shadow: -120px 2px 94px rgba(0, 0, 0, 0.13);
  opacity: 0.05;
}

.up {
  left: 6%;
  top: 32%;
}

.current {
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  color: #ffffff;
}
